/* styles.css */

.quiz-container-basic {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #282c34;
  }
  
  .basic-quiz-box {
    width: 600px;
    height: 400px; /* Set a fixed height */
    padding: 20px;
    border-radius: 10px;
    background-color: #1c1e22;
    color: white;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    overflow-y: auto; /* Enable vertical scrolling if content exceeds the height */
    margin-top: -150px; /* Adjust the margin to move the quiz box down */
  }
  
  .progress-bar-container {
    width: 100%;
    background-color: #333;
    border-radius: 5px;
    margin: 10px 0;
  }
  
  .progress-bar {
    height: 10px;
    background-color: #4CAF50;
    border-radius: 5px;
  }
  
  .option-button {
    display: block;
    width: 100%;
    padding: 10px;
    margin-top: 10px;
    font-size: 16px;
    cursor: pointer;
    background-color: #2c3038;
    border: none;
    color: white;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    transition: background-color 0.3s;
  }
  
  .option-button:hover {
    background-color: #3a3d43;
  }
  
  .other-input {
    width: 100%;
    padding: 10px;
    margin-top: 10px;
  }
  
  .other-submit {
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    background-color: #4CAF50;
    border: none;
    border-radius: 5px;
    color: white;
    margin-top: 10px;
  }
  